body { margin: 0; padding: 0; font-family: sans-serif; background-color: #fbfbfb;}

.content-box { width: 500px; margin: 10px auto; font-size: 14px;
background-color: #fff; border: 1px solid #f1f1f1; padding: 20px; }

.comment-input { background-color: #fff; border: 1px solid #f1f1f1;
padding: 20px; margin-bottom: 10px; }

.comment-field { margin-bottom: 15px; display: -ms-flexbox; display: flex; }

.comment-field .comment-field-name { display: -ms-flexbox; display: flex; -ms-flex-preferred-size: 100px; flex-basis: 100px; font-size: 14px; }
.comment-field .comment-field-input { display: -ms-flexbox; display: flex; -ms-flex: 1 1; flex: 1 1; }
.comment-field .comment-field-input input,
.comment-field .comment-field-input textarea {
  border: 1px solid #e6e6e6;
  border-radius: 3px;
  padding: 5px;
  outline: none;
  font-size: 14px;
  resize: none;
  -ms-flex: 1 1;
      flex: 1 1;
}
.comment-field .comment-field-input textarea { height: 100px; }

.comment-field-button { display: -ms-flexbox; display: flex; -ms-flex-pack: end; justify-content: flex-end; }
.comment-field-button button{padding: 5px 10px; width: 80px; border: none; border-radius: 3px;
background-color: #00a3cf; color: #fff; outline: none; cursor: pointer;}
.comment-field-button button:active{ background-color: #13c1f1;}

.comment { position: relative; display: -ms-flexbox; display: flex; border-bottom: 1px solid #f1f1f1;
margin-bottom: 10px; padding-bottom: 10px; min-height: 50px; }
.comment .comment-user { -ms-flex-negative: 0; flex-shrink: 0; }
.comment span.comment-username { color: #00a3cf; font-style: italic; }
.comment .comment-createdtime { padding: 5px; position: absolute; bottom: 0; right: 0; font-size: 12px; }
.comment .comment-delete { position: absolute; right: 0; top: 0;color: transparent; font-size: 12px; cursor: pointer; }
.comment:hover .comment-delete { color: #00a3cf;}
.comment p { margin: 0; text-indent: 1em; }
code { boder: 1px solid #ccc; background-color: #f9f9f9; padding: 0px 2px; }






